import { Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, CardGroup, Button, Row, Col, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { useState , useEffect} from "react";
import axios from "axios"
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import { useNavigate } from "react-router";
import { deletertrainingplan ,gettrainingplan} from "../../api/user";
const About = () => {
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deleteIndex, setDeleteIndex] = useState(null);
  const [isUpdateModalOpen, setIsUpdateModalOpen] = useState(false);
  const [selectedBlogIndex, setSelectedBlogIndex] = useState(null);
  const [apiData, setApiData] = useState("");
  
  // const [isError, setIsError] = useState("");
  // const [blogData, setBlogData] = useState(BlogData);
  useEffect(() => {
    async function callApi() {
      try {
        const apiResult = await gettrainingplan();
        console.log(apiResult)
          setApiData(apiResult);
        } catch (error) {
          // setIsError(error.message);
          console.log(error)
        }
      }
      callApi();
    }, []);
  const toggleDeleteModal = (index) => {
    setDeleteIndex(index);
    setShowDeleteModal(!showDeleteModal);
  };
  const handleDelete = async () => {

    try {
      const deletedUserId = apiData[deleteIndex].id;
      console.log(deletedUserId)
      // Make the API call to delete the user
      await deletertrainingplan(deletedUserId);
      // Update the local state by removing the deleted user from the array
      //  const updatedApiData = [...apiData];
      //  updatedApiData.splice(deleteIndex, 1);
      // setApiData(updatedApiData);
      setApiData(apiData.filter((course) => course.id !== deletedUserId));
      // setFilteredData(filteredData.filter((course) => course.id !== deletedCourseId));
    } catch (error) {
      console.log(error);
    }
  
  setShowDeleteModal(false);
};
  const navigate = useNavigate();
    const handleedit = (actionType, courseId) => {
      console.log(courseId)
      if (actionType === "add") {
        navigate("../admintrainingplan", { state: { actionType } });
      } else if (actionType === "edit") {
        navigate("../admintrainingplan", { state: { actionType, courseId } });
      }
    };
  return (
    <div>
    {/* --------------------------------------------------------------------------------*/}
    {/* Card-1*/}
    {/* --------------------------------------------------------------------------------*/}
    <h4 className="mb-3 caliber-font" style={{marginTop:"60px"}}>Training Plans</h4>
    <button  onClick={() => handleedit("add")} className="btn btn-success caliber-font" style={{float:"right", marginTop:"-45px"}}>Add Plan</button>
    <Row>
      {apiData &&
        apiData.map((blg, index) => (
        <Col sm="6" lg="6" xl="3" key={index}>
        <Card style={{height:"95%"}}>
        <LazyLoadImage
                    loading="lazy"
                    src={blg.featuredImage}
                    effect="blur"
                    placeholderSrc={blg.featuredImage}
                    style={{
                      width: "100%", // Set the desired width
    height: "250px", // Set the desired height
    objectFit: "cover",
                    }}/>
    <CardBody className="p-2 caliber-font" style={{ height:"100%" }}>
    <div style={{height:"35px"}}>
    <CardTitle tag="h5">{blg.trainingPlanName}</CardTitle>
    </div>
    <div style={{height: "50px"}}>
    <CardSubtitle>{blg.description && blg.description.length > 40 ? blg.description.slice(0, 40) + '...' : blg.description}</CardSubtitle>

    </div>
     <div >
      <Button onClick={()=>handleedit("edit",blg.id)} color={blg.color}>Update </Button>
      <button onClick={() => toggleDeleteModal(index)} style={{backgroundColor: 'red', color: 'white' , float:"right", height:"37px", borderRadius:"8px", border:"1px solid red" }} > Remove</button>
      </div></CardBody>
  </Card>
        </Col>
      ))}
    </Row>
    {/* Modal */}
    <Modal isOpen={showDeleteModal} toggle={toggleDeleteModal}>
      <ModalHeader toggle={toggleDeleteModal}>Delete Training Plan</ModalHeader>
      <ModalBody>
        Are you sure you want to delete this Training Plan?
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={handleDelete}>
          Delete
        </Button>
        <Button color="secondary" onClick={toggleDeleteModal}>
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
 
</div>
  );
};

export default About;
